import { stopSubmit } from "redux-form";
import { usersAPI, profileAPI } from "../api/api";
const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';
const SAVE_PHOTO_SUCCESS = 'SAVE_PHOTO_SUCCESS';
 
let initialState = {
    posts: [
        {id: 0, message: 'Hi, how are you?', likesCount: 1},
        {id: 1, message: 'It\'s my first post', likesCount: 4},
        {id: 2, message: 'It\'s my second post', likesCount: 4},
        {id: 3, message: 'It\'s my third post', likesCount: 4},
        {id: 4, message: 'It\'s my fourth post', likesCount: 4}
    ],
    profile: null,
    status: 'huj'
}

const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST: return {
                ...state,
                posts: [...state.posts, {id: 5, message: action.newPostText, likesCount: 0}],
        }
        case SET_USER_PROFILE: return {
                ...state,
                profile: action.profile
        }
        case SET_STATUS: return {       // По экшену SET_STATUS мы берем текущий стейт (то что в initialState), делаем его копию,
                ...state,               // перезаписываем и выводим новый стейт. Но поле status мы перезаписываем с новыми значениями
                status: action.status   // которые получаем в action.status
        }
        case DELETE_POST: return {
            ...state,
            posts: state.posts.filter(id => id !== action.postId)
        }
        case SAVE_PHOTO_SUCCESS: return {
            ...state,
            profile: {...state.profile, photos: action.photos}
        }
        default: return state;
    } 
}

export const addPostActionCreator = (newPostText) => ({type: ADD_POST, newPostText})   // Если ф-ция возвращает объект, оборачиваем его в кр скобки
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile})
export const setStatus = (status) => ({type: SET_STATUS, status})
export const deletePost = (postId) => ({type: DELETE_POST, postId})
export const savePhotoSuccess = (photos) => ({type: SAVE_PHOTO_SUCCESS, photos})
export const saveProfileSuccess = (photos) => ({type: SAVE_PHOTO_SUCCESS, photos})

export const getUserProfile = (userId) => async (dispatch) => {
    let response = await usersAPI.getProfile(userId);
    dispatch(setUserProfile(response.data))
}
export const getStatus = (userId) => async (dispatch) => {
    let response = await profileAPI.getStatus(userId);
    dispatch(setStatus(response.data))
}
export const updateStatus = (status) => async (dispatch) => {
    //try {
    let response = await profileAPI.updateStatus(status);
    if(response.data.resultCode === 0)
    dispatch(setStatus(status))
    //} catch (error) {

    //}
    
}
export const savePhoto = (file) => async (dispatch) => {
    let response = await profileAPI.savePhoto(file);
    if(response.data.resultCode === 0)
    dispatch(savePhotoSuccess(response.data.data.photos))
}
export const saveProfile = (profile) => async (dispatch, getState) => {
    const userId = getState().auth.userId;
    const response = await profileAPI.saveProfile(profile);
    if(response.data.resultCode === 0) {
       dispatch(getUserProfile(userId)) 
    } else {
        dispatch(stopSubmit('edit-profile', {_error: response.data.messages[0]}))
        return Promise.reject(response.data.messages)
    }
}

export default profileReducer;