import React from "react";
import {Field, reduxForm } from "redux-form";
import { Input } from "../common/FormsControls/FormsControls";
import { required } from "../../utils/validators/validators";
import { connect } from "react-redux";
import { login } from "../../redux/auth-reducer";
import { Redirect } from "react-router-dom";
import s from './../../components/common/FormsControls/FormsControls.module.css'

const LoginForm = ({handleSubmit, error, captchaUrl}) => {
    return <form onSubmit={handleSubmit}>
            <div><Field component={Input} name={'email'} placeholder="Email" validate={[required]} /></div>
            <div><Field component={Input} name={'password'} placeholder="Password" validate={[required]} type='password'/></div>
            <div><Field component={Input} name={'rememberMe'} type={'checkbox'} validate={[required]}/> remember me </div>
            {captchaUrl && <img src={captchaUrl} />}
            {captchaUrl && 
            <div><Field component={Input} name={'captcha'} placeholder="Symbols from image" validate={[required]} /></div>
            }

            {error && <div className={s.formSummaryError}>{error}</div>}
            <div><button>Login</button></div>
        </form>
    
}

const LoginReduxForm = reduxForm({form: 'login'})(LoginForm)

const Login = (props) => {
    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe, formData.captcha)
    }
    if (props.isAuth) {
        return <Redirect to='/profile' />
    }
    return <div>
        <h1>LOGIN</h1>
        <LoginReduxForm onSubmit={onSubmit} captchaUrl={props.captchaUrl} /> 
    </div>
}
const mapStateToProps = (state) => ({
    captchaUrl: state.auth.captchaUrl,
    isAuth: state.auth.isAuth
})
export default connect(mapStateToProps, {login})(Login)